import React from 'react';

interface CarouselProps {
    id: string,
    images: Array<string>
}

export default ({ id, images }: CarouselProps) => {

    const indicators = images.map((image: string, index: number) => {
        return <li data-target={`#${id}`} data-slide-to={index} className={index == 0 ? 'active' : ''}></li>
    });


    const items = images.map((image: string, index: number) => {
        return <div className={`item ${index == 0 ? 'active' : ''}`}>
            <img src={image} />
        </div>
    })


    return <div id={id} className="carousel slide" data-ride="carousel" >
        <ol className="carousel-indicators">
            {indicators}
        </ol>

        <div className="carousel-inner" role="listbox">
            {items}
        </div>

        <a className="left carousel-control" href={`#${id}`} role="button" data-slide="prev">
            <span className="glyphicon glyphicon-chevron-left" aria-hidden="true"></span>
            <span className="sr-only">Previous</span>
        </a>
        <a className="right carousel-control" href={`#${id}`} role="button" data-slide="next">
            <span className="glyphicon glyphicon-chevron-right" aria-hidden="true"></span>
            <span className="sr-only">Next</span>
        </a>
    </div>
}