import React from 'react'

interface CardProps {
    title: string,
    subtitle?: string,
    children: React.ReactNode
}

export default ({title, subtitle, children}: CardProps) => (
    <div className="card">
        <div className="header bg-blue">
            <h2>
                {title}
                <small>{subtitle}</small>
            </h2>
        </div>
        {children}
    </div>
)