import React, { useState } from 'react';
import firebase from 'firebase';
import Card from '../Card/Card';
import { Seller } from '../../interfaces/Seller';
import Carousel from '../Carousel/Carousel';
import { Link } from 'react-router-dom';

import './CardSeller.css'
import { getFavoritos, setFavoritos } from '../../services/FavoritesService';

interface CardSellerProps {
    seller: Seller;
    favorite: boolean;
}

enum LinkType {
    PHONE, WHATSAPP, FACEBOOK, INSTRAGRAM
}

export default ({ seller, favorite }: CardSellerProps) => {
    const [isFavorite, setFavorite] = useState(favorite)

    const saveFavorito = () => {
        getFavoritos((result: Array<number>) => {
            const index = result.indexOf(seller.id);
            if (index > -1) {
                result.splice(index, 1);
                setFavorite(false);
                firebase.analytics().logEvent('removeFavorite', { add: false, seller: seller });
            } else {
                result.push(seller.id);
                setFavorite(true);
                firebase.analytics().logEvent('addFavorite', { add: true, seller: seller });
            }

            setFavoritos(result);
        })
    }

    const openLink = (link: string, type: LinkType) => {
        firebase.analytics().logEvent('openLink', { type, seller })
        window.open(link, '_blank');
    }

    const whatsappText = `Olá, encontrei seu estabelecimento no app *DDelivery*.`.replace(/\s/, '%20');

    return <Card
        title={seller.name}
        subtitle={seller.address}
    >
        <Carousel
            id={`carousel-${seller.id}`}
            images={seller.images}
        />
        <div style={{ 'padding': '16px' }}>
            <div className='row'>
                <div className='col-xs-2'>
                    <i
                        onClick={() => openLink(`tel:${seller.phone}`, LinkType.PHONE)}
                        className='text-gray waves-blue fa fa-2x fa-phone'
                    />
                </div>
                <div className='col-xs-2'>
                    <i
                        onClick={() => openLink(`https://api.whatsapp.com/send?phone=${seller.whatsapp}&text=${whatsappText}`, LinkType.WHATSAPP)}
                        className='text-gray waves-blue font-bold fa fa-2x fa-whatsapp'
                    />
                </div>
                <div className='col-xs-2'>
                    <i
                        onClick={() => openLink(seller.facebook, LinkType.FACEBOOK)}
                        className='text-gray waves-blue font-bold fa fa-2x fa-facebook-official'
                    />
                </div>
                <div className='col-xs-2'>
                    <i
                        onClick={() => openLink(seller.instagram, LinkType.INSTRAGRAM)}
                        className='text-gray font-bold fa fa-2x fa-instagram'
                    />
                </div>
                <div className='col-xs-2'>
                    <i onClick={() => saveFavorito()} className={`text-gray font-bold fa fa-2x ${isFavorite ? 'fa-heart' : 'fa-heart-o'}`}></i>
                </div>
                <div className='col-xs-2'>
                    <Link to={`catalog/${seller.id}`}>
                        <i className='text-gray font-bold fa fa-2x fa-cart-plus'></i>
                    </Link>
                </div>
            </div>
        </div>
    </Card>
}