import React, { useEffect, useState } from 'react'
import { getSellers } from '../../services/SellersService';
import { Seller } from '../../interfaces/Seller';
import CardSeller from '../../components/CardSeller/CardSeller';
import { getFavoritos } from '../../services/FavoritesService';

export default () => {
    const [sellers, setSellers] = useState<Seller[]>([]);
    const [favorites, setFavorites] = useState<number[]>([]);

    useEffect(() => {
        getSellers((result: Array<Seller>) => setSellers(result))
        getFavoritos((result: Array<number>) => setFavorites(result))
    }, [])

    return <div className='row remove-margin'>
        {sellers.map(seller => <div className='col-xs-12 col-md-3'>
            <CardSeller seller={seller} favorite={favorites.indexOf(seller.id) > -1} />
        </div>
        )}
    </div>
}